import { Link } from 'react-router-dom'


const Header = () => (
  <header>
    <nav>
      <h1>
        <Link to="/" style={{ textDecoration: 'none' }}>
          VALORANT
        </Link>
      </h1>
      <Link to="/agents" style={{ textDecoration: 'none' }}>
        All Agents
      </Link>
    </nav>
  </header>
)

export default Header
