import { useEffect, useState } from 'react'
import { useParams } from 'react-router'

// Note: `uuid` comes from the URL, courtesy of our router
const Agent = () => {
  const [data, setData] = useState({})
  const [role, setRole] = useState({})
  const [ability, setAbility] = useState([])
  const [abilityDesx, setAbilityDesx] = useState('')
  const [soundId, setSoundId] = useState()
  const { uuid }=useParams()
  
  const getData = async () => {
    const res = await fetch(`https://valorant-api.com/v1/agents/${uuid}`)
    const respdata = await res.json()
    setData(respdata.data)
    setRole(respdata.data.role)
    setAbility(respdata.data.abilities)
    setAbilityDesx(respdata.data.description)
    setSoundId(respdata.data.voiceLine.mediaList[0].id)
    // music =  new Audio(respdata.data.voiceLine.mediaList[0].wave)
  }

  useEffect(() => {
    getData();
  }, [])
  
  const infoClick = () => {   // Audio activation logic
    setAbilityDesx(data.description);
    const audio = new Audio(`https://media.valorant-api.com/sounds/${soundId}.wav`)
    audio.play();
  }
  const slotClick = (idx) => {
    setAbilityDesx(ability[idx].description)
  }

  const { displayName, bustPortrait } = data

  return (
    <main className="agent">
      <div
        className="imgBox"
        style={{ backgroundImage: `url(${bustPortrait})` }}
      ></div>
      <div className="contentBox">
        <div className="contentBox-container">
          <span className="small-role">{role.displayName}</span>
          <h1 className="name">{displayName}</h1>
          <div className="abilities">
            <div className="abl abl--1" onClick={() => infoClick()}>
              <div className="abl-assign">INFO</div>
              <div
                className="abl-img"
                style={{ backgroundImage: `url(${role.displayIcon})` }}
              ></div>
            </div>
            {ability.map((ablt, idx) => {
              const { slot, displayName, displayIcon, discription } = ablt
              return (
                <div
                  className={`abl abl-${idx}`}
                  onClick={() => slotClick(idx)}
                  key={displayName}
                >
                  <div className="abl-assign">{slot}</div>
                  <div
                    className="abl-img"
                    style={{ backgroundImage: `url(${displayIcon})` }}
                  ></div>
                </div>
              )
            })}
          </div>
          {/* Abilities End*/}
          <p className="desx">{abilityDesx}</p>
          <p className="role-desx">
            <span className="bold">{role.displayName}</span>
            {role.description}
          </p>
        </div>
      </div>
    </main>
  )
}

export default Agent
